/* =======================Styles for app=========================================== */

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

/* ======================Styling app component======================================= */

.App {
  border: 1px solid black;
  height: 500px;
  width: 275px;
  margin-top: 70px;
  margin-left: 520px;
  border-radius: 10px;
  background-color: #3f3f3f;
  color: gray;
  box-shadow: 0px 5px 11px 7px #3f3f3f;
}

.statusbar {
  background-color: whitesmoke;
}

/* =======================instruction box============================================== */

.Instruction {
  position: absolute;
  z-index: 10;
  border-radius: 6px;
  top: 70px;
  left: 34px;
  background-color: whitesmoke;
}

/* ===================Styling in app components ======================================= */

.battery {
  margin-left: 166px;
  height: 20px;
  width: 20px;
}

.cover {
  margin-top: 20px;
  margin-left: 20px;
  width: 237px;
}

.menu-block {
  position: relative;
  border: 1px solid black;
  width: 235px;
  height: 215px;
  background-color: white;
}

.menu-box {
  height: 194px;
  width: auto;
  margin-top: 0px;
  border-top: 2px solid black;
}

#menu-list {
  width: 40%;
  list-style: none;
  padding: 0px;
  display: inline-block;
  position: absolute;
  height: 85%;
  margin: 0px;
}

.list-item {
  margin-bottom: 15px;
}

#small-img {
  height: 100%;
  width: 60%;
  position: absolute;
  margin-left: 94px;
  margin-top: -35px;
}

#heading-list {
  margin: 0px;
  margin-bottom: 10px;
  width: 40%;
}

#img-div {
  width: 60%;
  display: inline-block;
  position: relative;
}

/* ========================Styling the controls buttons ======================= */

#outer {
  height: 240px;
  width: 236px;
  position: relative;
  background-color: #3f3f3f;
}

#mid {
  height: 160px;
  width: 160px;

  margin-left: 37px;
  border-radius: 80px;
  position: absolute;
  background-color: #dedede;
  margin-top: 48px;
}

#inner {
  position: absolute;
  height: 50px;
  width: 50px;
  border-radius: 50%;
  margin-top: 55px;
  margin-left: 55px;
  background-color: #4e4e4e;
}

/* ===========================Styling buttons================================ */

.menu-button {
  position: absolute;
  margin-left: 55px;
  margin-top: 20px;
}

.comp-image {
  height: 100%;
  width: 100%;
}

#prev {
  position: absolute;
  margin-top: 77px;
  margin-left: 20px;
}
#forward {
  position: absolute;
  margin-top: 77px;
  margin-left: 125px;
}
#play-pause {
  position: absolute;
  margin-top: 125px;
  margin-left: 73px;
}

.active {
  background-color: gainsboro;
}
